import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { IStock, IStockData } from '../types';

const SearchContainer = styled.div`
    position: relative;
    margin: 20px 0;
`;

const SearchInput = styled.input`
    padding: 10px;
    width: 300px;
    margin-right: 10px;
`;

const SearchButton = styled.button`
    padding: 10px 20px;
    background-color: #4CAF50;
    color: white;
    border: none;
    cursor: pointer;
    
    &:hover {
        background-color: #45a049;
    }
`;

const SuggestionsList = styled.ul`
    position: absolute;
    top: 100%;
    left: 0;
    width: 300px;
    max-height: 200px;
    overflow-y: auto;
    background-color: white;
    border: 1px solid #ddd;
    border-top: none;
    margin: 0;
    padding: 0;
    list-style: none;
    z-index: 1000;
`;

const SuggestionItem = styled.li`
    padding: 10px;
    cursor: pointer;
    border-bottom: 1px solid #eee;

    &:hover {
        background-color: #f5f5f5;
    }

    span {
        display: block;
        font-size: 0.8em;
        color: #666;
    }
`;

interface StockSearchProps {
    stocks: IStock[];
    onSearch: (symbol: string) => void;
}

const StockSearch: React.FC<StockSearchProps> = ({ stocks, onSearch }) => {
    const [searchTerm, setSearchTerm] = useState('');
    const [suggestions, setSuggestions] = useState<IStock[]>([]);
    const [showSuggestions, setShowSuggestions] = useState(false);

    useEffect(() => {
        if (searchTerm.length > 0) {
            const filtered = stocks.filter(stock =>
                stock.company.symbol.toLowerCase().includes(searchTerm.toLowerCase()) ||
                stock.company.name.toLowerCase().includes(searchTerm.toLowerCase())
            );
            setSuggestions(filtered);
            setShowSuggestions(true);
        } else {
            setSuggestions([]);
            setShowSuggestions(false);
        }
    }, [searchTerm, stocks]);

    const handleSelectSuggestion = (symbol: string) => {
        setSearchTerm(symbol);
        setShowSuggestions(false);
        onSearch(symbol);
    };

    return (
        <SearchContainer>
            <SearchInput
                type="text"
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                placeholder="Search by symbol or company name..."
            />
            <SearchButton onClick={() => onSearch(searchTerm)}>Search</SearchButton>

            {showSuggestions && suggestions.length > 0 && (
                <SuggestionsList>
                    {suggestions.map(stock => (
                        <SuggestionItem
                            key={stock.company.symbol}
                            onClick={() => handleSelectSuggestion(stock.company.symbol)}
                        >
                            {stock.company.symbol} - {stock.company.name}
                            <span>{stock.company.industry}</span>
                        </SuggestionItem>
                    ))}
                </SuggestionsList>
            )}
        </SearchContainer>
    );
};

export default StockSearch; 