import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { StockHolding, UserState, ITradeHistory } from '../types/user';
import { GAME_CONFIG } from '../config/gameConfig';

const initialState: UserState = {
    cash: GAME_CONFIG.INITIAL_BALANCE,
    holdings: {},
    totalValue: GAME_CONFIG.INITIAL_BALANCE,
    totalProfitLoss: 0,
    stats: {
        winRate: 0,
        totalProfitLoss: 0,
        totalTrades: 0,
        biggestWin: 0,
        biggestLoss: 0,
        successfulTrades: 0
    },
    achievements: [],
    tradeHistory: [],
    transactions: []
};

const userSlice = createSlice({
    name: 'user',
    initialState,
    reducers: {
        executeTrade: (state, action: PayloadAction<{
            symbol: string;
            quantity: number;
            price: number;
            type: 'BUY' | 'SELL';
        }>) => {
            const { symbol, quantity, price, type } = action.payload;
            const totalCost = quantity * price;

            // Update trade stats
            state.stats.totalTrades += 1;

            // Add to trade history
            const trade: ITradeHistory = {
                id: state.tradeHistory.length + 1,
                symbol,
                type,
                quantity,
                price,
                timestamp: new Date().toISOString(),
                total: totalCost
            };

            if (type === 'BUY') {
                if (state.cash < totalCost) return;

                state.cash -= totalCost;
                if (!state.holdings[symbol]) {
                    state.holdings[symbol] = {
                        symbol,
                        quantity: 0,
                        averagePrice: 0,
                        purchaseHistory: []
                    };
                }

                const holding = state.holdings[symbol];
                // Calculate new average price
                const oldTotal = holding.quantity * holding.averagePrice;
                const newTotal = quantity * price;
                const newQuantity = holding.quantity + quantity;
                const newAverage = (oldTotal + newTotal) / newQuantity;

                console.log('Buy calculation:', {
                    symbol,
                    oldQuantity: holding.quantity,
                    addedQuantity: quantity,
                    newQuantity,
                    oldAvgPrice: holding.averagePrice,
                    buyPrice: price,
                    oldTotal,
                    newTotal,
                    newAverage
                });

                holding.quantity = newQuantity;
                holding.averagePrice = newAverage;

                holding.purchaseHistory.push({
                    timestamp: new Date(),
                    price,
                    quantity
                });

                trade.profitLoss = 0; // No profit/loss for buy orders
            } else {
                // SELL logic
                const holding = state.holdings[symbol];
                if (!holding || holding.quantity < quantity) return;

                const profitLoss = (price - holding.averagePrice) * quantity;
                state.cash += totalCost;
                state.stats.totalProfitLoss += profitLoss;

                // Update win/loss stats
                if (profitLoss > 0) {
                    state.stats.successfulTrades += 1;
                    state.stats.biggestWin = Math.max(state.stats.biggestWin, profitLoss);
                } else {
                    state.stats.biggestLoss = Math.min(state.stats.biggestLoss, profitLoss);
                }

                // Update win rate
                state.stats.winRate = (state.stats.successfulTrades / state.stats.totalTrades) * 100;

                holding.quantity -= quantity;
                if (holding.quantity === 0) {
                    delete state.holdings[symbol];
                }

                trade.profitLoss = profitLoss;
            }

            state.tradeHistory.push(trade);
        },
        updatePortfolioValue: (state, action: PayloadAction<Record<string, number>>) => {
            // currentPrices is an object with symbol -> current price
            const currentPrices = action.payload;
            let totalValue = state.cash;
            let totalProfitLoss = 0;

            Object.values(state.holdings).forEach(holding => {
                const currentPrice = currentPrices[holding.symbol];
                if (currentPrice) {
                    const holdingValue = holding.quantity * currentPrice;
                    const holdingCost = holding.quantity * holding.averagePrice;
                    totalValue += holdingValue;
                    totalProfitLoss += holdingValue - holdingCost;
                }
            });

            state.totalValue = totalValue;
            state.totalProfitLoss = totalProfitLoss;
        },
        checkAchievements: (state) => {
            // Add achievement logic here
            if (state.stats.totalProfitLoss >= 5000 && !state.achievements.includes(' To The Moon')) {
                state.achievements.push(' To The Moon');
            }
            if (state.stats.totalTrades >= 100 && !state.achievements.includes(' Diamond Hands')) {
                state.achievements.push(' Diamond Hands');
            }
            // Add more achievements as needed
        },
        resetUser: (state) => {
            state.cash = GAME_CONFIG.INITIAL_BALANCE;
            state.totalValue = GAME_CONFIG.INITIAL_BALANCE;
            state.holdings = {};
            state.tradeHistory = [];
            state.totalProfitLoss = 0;
            state.stats = {
                winRate: 0,
                totalProfitLoss: 0,
                totalTrades: 0,
                biggestWin: 0,
                biggestLoss: 0,
                successfulTrades: 0
            };
            state.achievements = [];
        }
    }
});

export const { executeTrade, updatePortfolioValue, checkAchievements, resetUser } = userSlice.actions;
export default userSlice.reducer; 