import { IAlgorithmPipeline, IAlgorithmLayer, IAlgorithmContext, IPriceEffect } from '../types/algorithm';

export class AlgorithmPipeline implements IAlgorithmPipeline {
    private _layers: IAlgorithmLayer[] = [];

    get layers(): IAlgorithmLayer[] {
        return [...this._layers];
    }

    addLayer(layer: IAlgorithmLayer): void {
        this._layers.push(layer);
    }

    removeLayer(layerName: string): void {
        this._layers = this._layers.filter(layer => layer.name !== layerName);
    }

    predict(context: IAlgorithmContext): IPriceEffect {
        if (this._layers.length === 0) {
            return {
                price: context.currentPrice,
                confidence: 1,
                reason: "No algorithm layers present"
            };
        }

        // Get predictions from all layers
        const predictions = this._layers.map(layer => ({
            effect: layer.process(context),
            weight: layer.weight
        }));

        // Calculate weighted average price
        const totalWeight = predictions.reduce((sum, p) => sum + p.weight, 0);
        const weightedPrice = predictions.reduce((sum, p) =>
            sum + (p.effect.price * p.weight), 0) / totalWeight;

        // Calculate average confidence
        const avgConfidence = predictions.reduce((sum, p) =>
            sum + (p.effect.confidence * p.weight), 0) / totalWeight;

        // Combine reasons
        const reasons = predictions
            .map(p => p.effect.reason)
            .filter(reason => reason.length > 0)
            .join("; ");

        return {
            price: weightedPrice,
            confidence: avgConfidence,
            reason: reasons
        };
    }
} 