import { IAlgorithmLayer, IAlgorithmContext, IPriceEffect } from '../types/algorithm';

export abstract class BaseLayer implements IAlgorithmLayer {
    constructor(
        public readonly name: string,
        public weight: number = 1.0
    ) { }

    abstract process(context: IAlgorithmContext): IPriceEffect;

    protected validateWeight(): void {
        if (this.weight < 0 || this.weight > 1) {
            throw new Error(`Layer weight must be between 0 and 1, got ${this.weight}`);
        }
    }
} 