import { BaseLayer } from '../BaseLayer';
import { IAlgorithmContext, IPriceEffect } from '../../types/algorithm';

export class VolatilityLayer extends BaseLayer {
    constructor(
        private period: number = 14, // Default to 14 periods for volatility calculation
        weight: number = 0.3
    ) {
        super('Volatility', weight);
    }

    process(context: IAlgorithmContext): IPriceEffect {
        const { priceHistory, currentPrice } = context;

        if (priceHistory.length < this.period) {
            return {
                price: currentPrice,
                confidence: 0.5,
                reason: "Insufficient data for volatility calculation"
            };
        }

        // Calculate daily returns
        const returns = [];
        for (let i = 1; i < priceHistory.length; i++) {
            const returnValue = (priceHistory[i].price - priceHistory[i - 1].price) / priceHistory[i - 1].price;
            returns.push(returnValue);
        }

        // Calculate standard deviation of returns (volatility)
        const mean = returns.reduce((sum, val) => sum + val, 0) / returns.length;
        const squaredDiffs = returns.map(val => Math.pow(val - mean, 2));
        const variance = squaredDiffs.reduce((sum, val) => sum + val, 0) / returns.length;
        const volatility = Math.sqrt(variance);

        // Higher volatility should lead to larger price movements
        const volatilityImpact = (Math.random() * 2 - 1) * volatility;
        const newPrice = currentPrice * (1 + volatilityImpact);

        return {
            price: newPrice,
            confidence: 0.7,
            reason: `Volatility-based adjustment: ${(volatilityImpact * 100).toFixed(2)}%`
        };
    }
} 