import { IStockHolding, IPortfolio, IUserPortfolio } from '../types/portfolio';
import { IStock } from '../types';

export class Portfolio implements IUserPortfolio {
    private cash: number;
    private holdings: Map<string, IStockHolding>;
    private currentPrices: Map<string, number>;

    constructor(initialCash: number = 100000) {
        this.cash = initialCash;
        this.holdings = new Map();
        this.currentPrices = new Map();
    }

    getHolding(symbol: string): IStockHolding | undefined {
        return this.holdings.get(symbol);
    }

    getCash(): number {
        return this.cash;
    }

    getTotalValue(): number {
        let stocksValue = 0;
        this.holdings.forEach((holding, symbol) => {
            const currentPrice = this.getCurrentPrice(symbol);
            stocksValue += holding.quantity * currentPrice;
        });
        return this.cash + stocksValue;
    }

    getProfitLoss(): { value: number; percentage: number } {
        let totalCost = 0;
        let currentValue = 0;

        this.holdings.forEach((holding, symbol) => {
            totalCost += holding.totalCost;
            currentValue += holding.quantity * this.getCurrentPrice(symbol);
        });

        const profitLoss = currentValue - totalCost;
        const percentage = totalCost > 0 ? (profitLoss / totalCost) * 100 : 0;

        return { value: profitLoss, percentage };
    }

    executeTrade(type: 'BUY' | 'SELL', symbol: string, quantity: number, price: number): boolean {
        const total = quantity * price;

        if (type === 'BUY') {
            if (this.cash < total) {
                return false;
            }

            const holding = this.holdings.get(symbol) || {
                symbol,
                quantity: 0,
                averagePrice: 0,
                totalCost: 0,
                transactions: []
            };

            // Update holding
            const newTotalCost = holding.totalCost + total;
            const newQuantity = holding.quantity + quantity;
            holding.averagePrice = newTotalCost / newQuantity;
            holding.quantity = newQuantity;
            holding.totalCost = newTotalCost;

            holding.transactions.push({
                type,
                price,
                quantity,
                timestamp: new Date(),
                total
            });

            this.holdings.set(symbol, holding);
            this.cash -= total;
        } else {
            const holding = this.holdings.get(symbol);
            if (!holding || holding.quantity < quantity) {
                return false;
            }

            holding.quantity -= quantity;
            holding.transactions.push({
                type,
                price,
                quantity,
                timestamp: new Date(),
                total
            });

            if (holding.quantity === 0) {
                this.holdings.delete(symbol);
            }

            this.cash += total;
        }

        return true;
    }

    updateHoldingValue(symbol: string, currentPrice: number): void {
        this.currentPrices.set(symbol, currentPrice);
    }

    private getCurrentPrice(symbol: string): number {
        return this.currentPrices.get(symbol) || 0;
    }

    getHoldings(): Map<string, IStockHolding> {
        return new Map(this.holdings);
    }
} 