import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { NewsItem, NewsState } from '../types/news';

const initialState: NewsState = {
    items: [],
    currentNews: null,
    newsHistory: []
};

const newsSlice = createSlice({
    name: 'news',
    initialState,
    reducers: {
        addNews: (state, action: PayloadAction<NewsItem>) => {
            state.items.push(action.payload);
        },
        setCurrentNews: (state, action: PayloadAction<NewsItem>) => {
            state.currentNews = action.payload;
        },
        addToHistory: (state, action: PayloadAction<NewsItem>) => {
            state.newsHistory.push(action.payload);
        },
        clearCurrentNews: (state) => {
            state.currentNews = null;
        },
        resetNews: (state) => {
            state.currentNews = null;
            state.items = [];
            state.newsHistory = [];
        }
    }
});

export const {
    addNews,
    setCurrentNews,
    clearCurrentNews,
    resetNews,
    addToHistory
} = newsSlice.actions;
export default newsSlice.reducer; 