import { BaseLayer } from '../BaseLayer';
import { IAlgorithmContext, IPriceEffect } from '../../types/algorithm';

export class MovingAverageLayer extends BaseLayer {
    constructor(
        private period: number = 20,  // Default to 20-period MA
        weight: number = 1.0
    ) {
        super('MovingAverage', weight);
    }

    process(context: IAlgorithmContext): IPriceEffect {
        const relevantPrices = context.priceHistory
            .slice(-this.period)
            .map(h => h.price);

        if (relevantPrices.length < this.period) {
            return {
                price: context.currentPrice,
                confidence: 0.5,
                reason: `Insufficient data for ${this.period}-period moving average`
            };
        }

        const ma = relevantPrices.reduce((sum, price) => sum + price, 0) / this.period;
        const trend = ma > context.currentPrice ? 'upward' : 'downward';
        const deviation = Math.abs(ma - context.currentPrice) / context.currentPrice;

        return {
            price: ma,
            confidence: Math.max(0.6, 1 - deviation),  // Higher confidence when close to current price
            reason: `${this.period}-period MA suggests ${trend} trend`
        };
    }
} 