import BaseEntity from './BaseEntity';
import { IStock, IStockData, ICompany, IVolumeData } from '../types';

/**
 * Stock entity represents a tradable stock in the market
 * It contains company information and price data
 */
export default class Stock extends BaseEntity implements IStockData {
    company: ICompany;
    currentPrice: number;
    previousClose: number;
    dayHigh: number;
    dayLow: number;
    volume: number;
    volatility: number;
    tradingVolume: Array<{ timestamp: Date; volume: number }>;
    priceHistory: Array<{ timestamp: Date; price: number }>;
    volumeHistory: IVolumeData[] = [];
    currentBuyVolume: number = 0;
    currentSellVolume: number = 0;
    private lastVolumeUpdate: Date = new Date();
    private readonly VOLUME_UPDATE_INTERVAL = 5000; // 5 seconds for each volume bar
    private readonly VOLUME_INTERVAL = 30000; // 30 seconds for each volume bar
    private currentIntervalVolumes = {
        buyVolume: 0,
        sellVolume: 0,
        startTime: new Date()
    };
    private readonly MAX_HISTORY_LENGTH = 100; // Limit history length

    /**
     * Creates a new Stock instance
     * @param company - Company information
     * @param initialPrice - Initial stock price
     */
    constructor(company: ICompany, initialPrice: number) {
        super();
        this.company = company;
        this.currentPrice = initialPrice;
        this.previousClose = initialPrice;
        this.dayHigh = initialPrice;
        this.dayLow = initialPrice;
        this.volume = 0;
        this.volatility = 0;
        this.tradingVolume = [];
        this.priceHistory = [{
            timestamp: new Date(),
            price: initialPrice
        }];
        // Initialize volume history with separate buy and sell entries
        const now = new Date();
        this.volumeHistory = [
            {
                timestamp: now,
                tradeVolume: 0,
                type: 'BUY'
            },
            {
                timestamp: now,
                tradeVolume: 0,
                type: 'SELL'
            }
        ];
        this.lastVolumeUpdate = now;
    }

    /**
     * Updates the stock price and related metrics
     * @param newPrice - New stock price
     */
    updatePrice(newPrice: number): void {
        const timestamp = new Date();

        //   previousClose 
        if (this.priceHistory.length === 0) {
            this.previousClose = this.currentPrice;
        }

        this.currentPrice = Math.round(newPrice * 100) / 100;

        //   
        this.priceHistory.push({
            timestamp,
            price: this.currentPrice
        });

        //  24  
        const oneDayAgo = new Date(timestamp.getTime() - 24 * 60 * 60 * 1000);
        this.priceHistory = this.priceHistory.filter(p => p.timestamp > oneDayAgo);

        console.log('Stock updatePrice:', {
            symbol: this.company.symbol,
            oldPrice: this.previousClose,
            newPrice: this.currentPrice,
            historyLength: this.priceHistory.length
        });
    }

    private updateVolatility(): void {
        if (this.priceHistory.length < 2) {
            this.volatility = 0;
            return;
        }

        const returns = [];
        for (let i = 1; i < this.priceHistory.length; i++) {
            const returnValue = (this.priceHistory[i].price - this.priceHistory[i - 1].price)
                / this.priceHistory[i - 1].price;
            returns.push(returnValue);
        }

        const mean = returns.reduce((sum, val) => sum + val, 0) / returns.length;
        const squaredDiffs = returns.map(val => Math.pow(val - mean, 2));
        const variance = squaredDiffs.reduce((sum, val) => sum + val, 0) / returns.length;
        this.volatility = Math.sqrt(variance);
    }

    /**
     * Calculates the price change percentage
     * @returns number - Price change percentage
     */
    getPriceChangePercentage(): number {
        return ((this.currentPrice - this.previousClose) / this.previousClose) * 100;
    }

    calculateNextPrice(): number {
        // Base random movement (-0.5% to 0.5%)
        const randomFactor = (Math.random() - 0.5) * 0.02; // Increased to 2% max change

        // Volume impact (-0.5% to 0.5% based on buy/sell ratio)
        const totalVolume = this.currentIntervalVolumes.buyVolume + this.currentIntervalVolumes.sellVolume;
        let volumeImpact = 0;
        if (totalVolume > 0) {
            const buyRatio = this.currentIntervalVolumes.buyVolume / totalVolume;
            volumeImpact = (buyRatio - 0.5) * 0.02;
        }

        // Volatility impact
        const volatilityImpact = (Math.random() - 0.5) * this.volatility * 0.03;

        // Combine all factors
        const totalChange = randomFactor + volumeImpact + volatilityImpact;
        const newPrice = this.currentPrice * (1 + totalChange);

        // Add some logging to debug
        console.log(`Price update for ${this.company.symbol}:`, {
            oldPrice: this.currentPrice,
            newPrice,
            change: totalChange * 100,
            factors: { randomFactor, volumeImpact, volatilityImpact }
        });

        return newPrice;
    }

    addVolume(volume: number, type: 'BUY' | 'SELL'): void {
        const now = new Date();
        console.log('Adding volume:', {
            symbol: this.company.symbol,
            type,
            volume,
            currentIntervalVolumes: this.currentIntervalVolumes,
            historyLength: this.volumeHistory.length
        });

        // Update current interval volumes
        if (type === 'BUY') {
            this.currentIntervalVolumes.buyVolume += volume;
            this.currentBuyVolume = volume;
        } else {
            this.currentIntervalVolumes.sellVolume += volume;
            this.currentSellVolume = volume;
        }

        this.volume += volume;

        // Always add new volume data point
        if (true) {
            console.log('Creating new volume interval:', {
                symbol: this.company.symbol,
                buyVolume: this.currentIntervalVolumes.buyVolume,
                sellVolume: this.currentIntervalVolumes.sellVolume,
                timestamp: now
            });

            this.volumeHistory.push({
                timestamp: now,
                tradeVolume: this.currentIntervalVolumes.buyVolume,
                type: 'BUY'
            });

            this.volumeHistory.push({
                timestamp: now,
                tradeVolume: this.currentIntervalVolumes.sellVolume,
                type: 'SELL'
            });

            this.lastVolumeUpdate = now;

            // Reset for next interval
            this.currentIntervalVolumes = {
                buyVolume: 0,
                sellVolume: 0,
                startTime: now
            };
        }

        // Keep history within limits
        if (this.volumeHistory.length > this.MAX_HISTORY_LENGTH) {
            this.volumeHistory = this.volumeHistory.slice(-this.MAX_HISTORY_LENGTH);
        }
    }

    private updateVolumeHistory(): void {
        const now = new Date();

        // Add separate entries for buy and sell volumes
        this.volumeHistory.push({
            timestamp: now,
            tradeVolume: this.currentIntervalVolumes.buyVolume,
            type: 'BUY'
        });

        this.volumeHistory.push({
            timestamp: now,
            tradeVolume: this.currentIntervalVolumes.sellVolume,
            type: 'SELL'
        });
    }
} 