import React, { useEffect, useState, useCallback } from 'react';
import styled from 'styled-components';
import StockChart from '../components/StockChart';
import StockSearch from '../components/StockSearch';
import TradePanel from '../components/TradePanel';
import Portfolio from '../components/Portfolio';
import StockHoldingInfo from '../components/StockHoldingInfo';
import Market from '../entities/Market';
import { IStock, IStockData, IVolumeData } from '../types';
import { createInitialStocks } from '../data/sampleStocks';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../store/store';
import { updateStocks, setSelectedStock } from '../store/stocksSlice';
import StockClass from '../entities/Stock';
import { useLocation } from 'react-router-dom';
import { GAME_CONFIG } from '../config/gameConfig';
import { startGame, resetGame } from '../store/gameSlice';
import { setMarketInstance } from '../store/marketInstanceSlice';
import GameStateScreen from '../components/GameStateScreen';

const PageContainer = styled.div`
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
`;

const StockInfo = styled.div`
    margin: 20px 0;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 4px;
`;

const StockGrid = styled.div`
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 20px;
    margin: 20px 0;
`;

const StockCard = styled.div`
    padding: 15px;
    border: 1px solid #ddd;
    border-radius: 4px;
    cursor: pointer;
    
    &:hover {
        background-color: #f5f5f5;
    }
`;

const StartContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 80vh;
    padding: 2rem;
`;

const PepeImage = styled.img`
    width: 200px;
    height: 200px;
    border-radius: 50%;
    margin-bottom: 2rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
`;

const GameDescription = styled.p`
    font-size: 1.2rem;
    text-align: center;
    margin: 1.5rem 0;
    color: #666;
    max-width: 600px;
    line-height: 1.6;
`;

const StartButton = styled.button`
    padding: 1rem 2rem;
    font-size: 1.2rem;
    background: #4CAF50;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease;

    &:hover {
        background: #45a049;
        transform: translateY(-2px);
    }
`;

const MarketPage: React.FC = () => {
    const dispatch = useDispatch();
    const stocks = useSelector((state: RootState) => state.stocks.stocks);
    const selectedStock = useSelector((state: RootState) => state.stocks.selectedStock);
    const isGameStarted = useSelector((state: RootState) => state.game.isStarted);
    const marketInstance = useSelector((state: RootState) => state.marketInstance.instance);
    const location = useLocation();
    const [timeLeft, setTimeLeft] = useState(GAME_CONFIG.GAME_DURATION);
    const gameStartTime = useSelector((state: RootState) => state.game.startTime);

    const initializeMarket = useCallback(() => {
        console.log('Initializing market...');
        const newMarket = new Market();
        const initialStocks = createInitialStocks();

        initialStocks.forEach(stock => {
            newMarket.addStock(stock);
        });

        const marketData = newMarket.getMarketData();
        dispatch(updateStocks(Object.values(marketData.stocks)));
        dispatch(setMarketInstance(newMarket));
        newMarket.startGame();
        dispatch(startGame());
    }, [dispatch]);

    // Market update effect
    useEffect(() => {
        if (!marketInstance || !isGameStarted) return;

        console.log('Market update effect triggered', {
            marketExists: !!marketInstance,
            isGameStarted,
            stocksInRedux: Object.keys(stocks).length
        });

        const updateUI = () => {
            const marketData = marketInstance.getMarketData();
            const stocksData = Object.values(marketData.stocks);
            dispatch(updateStocks(stocksData));
        };

        updateUI();
        const interval = setInterval(updateUI, GAME_CONFIG.PRICE_UPDATE_INTERVAL);

        return () => {
            clearInterval(interval);
        };
    }, [marketInstance, dispatch, isGameStarted]);

    // Add debug logging
    useEffect(() => {
        console.log('Current stocks in Redux:', Object.keys(stocks).length);
    }, [stocks]);

    // Debug log for chart data
    useEffect(() => {
        if (selectedStock) {
            console.log('Chart data:', {
                priceHistory: selectedStock.priceHistory,
                volumeHistory: selectedStock.volumeHistory,
                currentPrice: selectedStock.currentPrice
            });
        }
    }, [selectedStock]);

    // Log Redux state changes
    useEffect(() => {
        console.log('Redux State Update:', {
            stocksCount: Object.keys(stocks).length,
            selectedSymbol: selectedStock?.company.symbol,
            selectedPrice: selectedStock?.currentPrice,
            historyLength: selectedStock?.priceHistory.length
        });
    }, [stocks, selectedStock]);

    //        
    useEffect(() => {
        if (location.pathname === '/') {
            dispatch(setSelectedStock(null));
        }
    }, [location, dispatch]);

    // Timer effect
    useEffect(() => {
        if (!isGameStarted || !gameStartTime) return;

        const timer = setInterval(() => {
            const elapsed = Date.now() - gameStartTime;
            const remaining = GAME_CONFIG.GAME_DURATION - elapsed;

            if (remaining <= 0) {
                clearInterval(timer);
                setTimeLeft(0);
                dispatch(resetGame());
                return;
            }

            setTimeLeft(remaining);
        }, 100); //     100ms  

        return () => clearInterval(timer);
    }, [isGameStarted, gameStartTime, dispatch]);

    // Real-time stock update effect for selected stock
    useEffect(() => {
        if (!selectedStock || !marketInstance) return;

        const updateSelectedStock = () => {
            const updatedStock = marketInstance.getStock(selectedStock.company.symbol);
            if (updatedStock) {
                dispatch(setSelectedStock(updatedStock));
            }
        };

        // Initial update
        updateSelectedStock();

        // Set up interval for updates
        const interval = setInterval(updateSelectedStock, GAME_CONFIG.PRICE_UPDATE_INTERVAL);

        return () => clearInterval(interval);
    }, [selectedStock?.company.symbol, marketInstance, dispatch]);

    const getStockInstance = useCallback((symbol: string): IStock | undefined => {
        return marketInstance?.getStock(symbol);
    }, [marketInstance]);

    const handleStockSelect = (stock: IStock) => {
        dispatch(setSelectedStock(stock));
    };

    const handleTrade = useCallback((type: 'BUY' | 'SELL', quantity: number) => {
        if (!selectedStock || !marketInstance) return;

        const currentStock = stocks[selectedStock.company.symbol];
        if (!currentStock) return;

        if (type === 'BUY') {
            const result = marketInstance.executeBuyOrder(selectedStock.company.symbol, quantity);
            alert(result.message);
        } else {
            const result = marketInstance.executeSellOrder(selectedStock.company.symbol, quantity);
            alert(result.message);
        }
    }, [marketInstance, selectedStock, stocks]);

    // Helper function to calculate price change percentage
    const calculatePriceChange = (stock: IStockData): number => {
        return ((stock.currentPrice - stock.previousClose) / stock.previousClose) * 100;
    };

    // Format time for display
    const formatTime = (ms: number) => {
        const seconds = Math.floor(ms / 1000);
        const minutes = Math.floor(seconds / 60);
        const remainingSeconds = seconds % 60;
        return `${minutes}:${remainingSeconds.toString().padStart(2, '0')}`;
    };

    //     
    if (!isGameStarted || timeLeft <= 0) {
        return <GameStateScreen type={timeLeft <= 0 ? "end" : "start"} onStart={initializeMarket} />;
    }

    if (!marketInstance || Object.keys(stocks).length === 0) {
        return <div>Loading market data...</div>;
    }

    return (
        <PageContainer>
            <Portfolio />
            <h1>Stock Market</h1>
            <StockSearch
                stocks={Object.values(stocks).map(stockData => getStockInstance(stockData.company.symbol)!)}
                onSearch={(symbol) => {
                    const stock = getStockInstance(symbol);
                    if (stock) dispatch(setSelectedStock(stock));
                }}
            />

            {!selectedStock && (
                <>
                    <h2>Featured Stocks</h2>
                    <StockGrid>
                        {Object.values(stocks).slice(0, 6).map(stock => (
                            <StockCard
                                key={stock.company.symbol}
                                onClick={() => handleStockSelect(stock)}
                            >
                                <h3>{stock.company.symbol}</h3>
                                <p>{stock.company.name}</p>
                                <p>${stock.currentPrice.toFixed(2)}</p>
                                <p>{calculatePriceChange(stock).toFixed(2)}%</p>
                            </StockCard>
                        ))}
                    </StockGrid>
                </>
            )}

            {selectedStock && (
                <>
                    <StockInfo>
                        <h2>{selectedStock.company.name} ({selectedStock.company.symbol})</h2>
                        <p>{selectedStock.company.description}</p>
                        <div>Current Price: ${selectedStock.currentPrice.toFixed(2)}</div>
                        <div>Change: {calculatePriceChange(selectedStock).toFixed(2)}%</div>
                    </StockInfo>

                    <StockHoldingInfo symbol={selectedStock.company.symbol} />

                    <StockChart
                        data={selectedStock.priceHistory}
                        volumeData={selectedStock.volumeHistory}
                    />
                    <TradePanel
                        stock={getStockInstance(selectedStock.company.symbol)!}
                        onTrade={handleTrade}
                    />
                </>
            )}
        </PageContainer>
    );
};

export default MarketPage; 