import React, { useMemo } from 'react';
import { BarChart, Bar, XAxis, YAxis, Tooltip, ResponsiveContainer, CartesianGrid, Legend } from 'recharts';
import styled from 'styled-components';
import { IVolumeData } from '../types';

const ChartContainer = styled.div`
    height: 150px;
    width: 100%;
    background: white;
    padding: 10px;
    border-radius: 8px;
`;

const VolumeLabel = styled.div`
    font-size: 12px;
    color: #666;
    margin-bottom: 5px;
`;

interface VolumeChartProps {
    data: IVolumeData[];
}

const VolumeChart: React.FC<VolumeChartProps> = ({ data }) => {
    // Debug log
    console.log('Volume data:', data);

    const chartData = useMemo(() =>
        data.reduce((acc, item) => {
            const timeStr = new Date(item.timestamp).toLocaleTimeString();
            const existing = acc.find(d => d.time === timeStr);

            if (existing) {
                if (item.type === 'BUY') {
                    existing.buyVolume = item.tradeVolume;
                } else {
                    existing.sellVolume = item.tradeVolume;
                }
                existing.total = existing.buyVolume + existing.sellVolume;
            } else {
                acc.push({
                    time: timeStr,
                    buyVolume: item.type === 'BUY' ? item.tradeVolume : 0,
                    sellVolume: item.type === 'SELL' ? item.tradeVolume : 0,
                    total: item.tradeVolume
                });
            }
            return acc;
        }, [] as Array<{
            time: string;
            buyVolume: number;
            sellVolume: number;
            total: number;
        }>),
        [data]
    );

    return (
        <ChartContainer>
            <VolumeLabel>Volume</VolumeLabel>
            <ResponsiveContainer>
                <BarChart data={chartData}>
                    <CartesianGrid strokeDasharray="3 3" />
                    <XAxis
                        dataKey="time"
                        interval="preserveStartEnd"
                        tick={{ fontSize: 10 }}
                    />
                    <YAxis
                        tick={{ fontSize: 10 }}
                        tickFormatter={value => `${(value / 1000).toFixed(0)}K`}
                    />
                    <Tooltip
                        formatter={(value: number, name: string) => [
                            value.toLocaleString(),
                            name === 'buyVolume' ? 'Buy Volume' : 'Sell Volume'
                        ]}
                        labelFormatter={time => `Time: ${time}`}
                    />
                    <Legend />
                    <Bar
                        dataKey="buyVolume"
                        fill="#82ca9d"
                        stackId="volume"
                        name="Buy Volume"
                    />
                    <Bar
                        dataKey="sellVolume"
                        fill="#ff8884"
                        stackId="volume"
                        name="Sell Volume"
                    />
                </BarChart>
            </ResponsiveContainer>
        </ChartContainer>
    );
};

export default VolumeChart; 