import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { IStock } from '../types';

interface StocksState {
    stocks: Record<string, IStock>;
    selectedStock: IStock | null;
    lastUpdate: Date;
}

const initialState: StocksState = {
    stocks: {},
    selectedStock: null,
    lastUpdate: new Date()
};

const stocksSlice = createSlice({
    name: 'stocks',
    initialState,
    reducers: {
        updateStocks: (state, action: PayloadAction<IStock[]>) => {
            const newStocks = action.payload.reduce((acc, stock) => {
                acc[stock.company.symbol] = stock;
                return acc;
            }, {} as Record<string, IStock>);

            // Only update if there are actual changes
            const hasChanges = Object.keys(newStocks).some(symbol => {
                const oldStock = state.stocks[symbol];
                const newStock = newStocks[symbol];
                return !oldStock || oldStock.currentPrice !== newStock.currentPrice;
            });

            if (hasChanges) {
                state.stocks = newStocks;
                state.lastUpdate = new Date();
            }
        },
        updateStock: (state, action: PayloadAction<IStock>) => {
            state.stocks[action.payload.company.symbol] = action.payload;
            if (state.selectedStock?.company.symbol === action.payload.company.symbol) {
                state.selectedStock = action.payload;
            }
        },
        setSelectedStock: (state, action: PayloadAction<IStock | null>) => {
            state.selectedStock = action.payload;
        },
        resetStocks: (state) => {
            state.stocks = {};
            state.selectedStock = null;
        }
    }
});

export const { updateStocks, updateStock, setSelectedStock, resetStocks } = stocksSlice.actions;
export default stocksSlice.reducer; 