import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import Market from '../entities/Market';

interface MarketInstanceState {
    instance: Market | null;
}

const initialState: MarketInstanceState = {
    instance: null
};

const marketInstanceSlice = createSlice({
    name: 'marketInstance',
    initialState,
    reducers: {
        setMarketInstance: (state, action: PayloadAction<Market>) => {
            state.instance = action.payload;
        }
    }
});

export const { setMarketInstance } = marketInstanceSlice.actions;
export default marketInstanceSlice.reducer; 