import React from 'react';
import styled from 'styled-components';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../store/store';
import { resetGame } from '../store/gameSlice';
import { GAME_CONFIG } from '../config/gameConfig';
import { resetStocks } from '../store/stocksSlice';
import { resetUser } from '../store/userSlice';
import { resetNews } from '../store/newsSlice';
import kingPepe from '../images/king_pepe.webp';
import diamondPepe from '../images/diamond-pepe.png';
import thumbsUpPepe from '../images/thumbs-up-pepe.png';
import sweatPepe from '../images/sweat-pepe.png';
import clownPepe from '../images/clown-pepe.png';

const Container = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 80vh;
    padding: 2rem;
`;

const CircleContainer = styled.div`
    width: 200px;
    height: 200px;
    border-radius: 50%;
    margin-bottom: 2rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    display: flex;
    align-items: center;
    justify-content: center;
    background: white;
    overflow: hidden;
`;

const BadgeImage = styled.img`
    width: 100%;
    height: 100%;
    object-fit: cover;
`;

const Message = styled.p`
    font-size: 1.2rem;
    text-align: center;
    margin: 1.5rem 0;
    color: #666;
    max-width: 600px;
    line-height: 1.6;
`;

const ActionButton = styled.button`
    padding: 1rem 2rem;
    font-size: 1.2rem;
    background: #4CAF50;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease;

    &:hover {
        background: #45a049;
        transform: translateY(-2px);
    }
`;

interface Props {
    type: 'start' | 'end';
    onStart?: () => void;
}

const START_MESSAGE = `Listen up, anon! You've got 1 minute to become the next PePe Buffett. 
Buy low, sell high, and don't be a paper hands! 
Remember: Stonks only go up... except when they don't! `;

const getBadgeAndMessage = (returnRate: number) => {
    if (returnRate >= 50) return {
        badge: kingPepe,
        message: "Holy moly! Even PePe Buffett should learn from you! True Diamond Hands!"
    };
    if (returnRate >= 20) return {
        badge: diamondPepe,
        message: "Amazing! The real Wolf of Wall Street right here!"
    };
    if (returnRate >= 0) return {
        badge: thumbsUpPepe,
        message: "Positive returns! You've got potential, keep it up!"
    };
    if (returnRate >= -20) return {
        badge: sweatPepe,
        message: "It's okay! Everyone starts somewhere. Practice makes perfect!"
    };
    return {
        badge: clownPepe,
        message: "Uhm... Are you testing the app? Maybe practice a bit more before real trading..."
    };
};

const GameStateScreen: React.FC<Props> = ({ type, onStart }) => {
    const dispatch = useDispatch();
    const { cash, holdings } = useSelector((state: RootState) => state.user);
    const stocks = useSelector((state: RootState) => state.stocks.stocks);
    const initialBalance = GAME_CONFIG.INITIAL_BALANCE;

    //      
    const holdingsValue = Object.entries(holdings).reduce((total, [symbol, holding]) => {
        const currentPrice = stocks[symbol]?.currentPrice || 0;
        return total + (currentPrice * holding.quantity);
    }, 0);

    //   ( +   )
    const totalValue = cash + holdingsValue;

    //  
    const returnRate = ((totalValue - initialBalance) / initialBalance) * 100;

    const { badge, message } = getBadgeAndMessage(returnRate);

    const handleAction = () => {
        if (type === 'start') {
            onStart?.();
        } else {
            dispatch(resetGame());
            dispatch(resetStocks());
            dispatch(resetUser());
            dispatch(resetNews());

            setTimeout(() => {
                onStart?.();
            }, 100);
        }
    };

    return (
        <Container>
            <CircleContainer>
                {type === 'start' ? (
                    <BadgeImage src={kingPepe} alt="King Pepe" />
                ) : (
                    <BadgeImage src={badge} alt="Result Badge" />
                )}
            </CircleContainer>
            <Message>
                {type === 'start' ? START_MESSAGE : (
                    <>
                        {message}
                        <br />
                        <small>
                            Cash: ${cash.toLocaleString()}
                            <br />
                            Holdings: ${holdingsValue.toLocaleString()}
                            <br />
                            Total Return: {returnRate.toFixed(2)}%
                        </small>
                    </>
                )}
            </Message>
            <ActionButton onClick={handleAction}>
                {type === 'start' ? 'Start Trading' : 'Try Again'}
            </ActionButton>
        </Container>
    );
};

export default GameStateScreen; 