import { BaseLayer } from '../BaseLayer';
import { IAlgorithmContext, IPriceEffect } from '../../types/algorithm';

export class NewsAnalysisLayer extends BaseLayer {
    private readonly sentimentWeights = {
        positive: 0.02,  // 2% price increase
        negative: -0.02, // 2% price decrease
        neutral: 0
    };

    constructor(weight: number = 1.0) {
        super('NewsAnalysis', weight);
    }

    process(context: IAlgorithmContext): IPriceEffect {
        const recentEvents = context.events
            .filter(event => {
                const eventAge = Date.now() - event.timestamp.getTime();
                return eventAge < 24 * 60 * 60 * 1000; // Last 24 hours
            })
            .sort((a, b) => b.timestamp.getTime() - a.timestamp.getTime());

        if (recentEvents.length === 0) {
            return {
                price: context.currentPrice,
                confidence: 0.5,
                reason: "No recent news events"
            };
        }

        let totalImpact = 0;
        let totalTrust = 0;
        const reasons: string[] = [];

        recentEvents.forEach(event => {
            const sentiment = this.analyzeSentiment(event.content);
            const impact = this.sentimentWeights[sentiment] * event.trustScore;

            totalImpact += impact;
            totalTrust += event.trustScore;

            if (Math.abs(impact) > 0.005) {  // Only include significant impacts
                reasons.push(`${event.source}: ${sentiment} impact`);
            }
        });

        const averageImpact = totalImpact / recentEvents.length;
        const newPrice = context.currentPrice * (1 + averageImpact);
        const confidence = Math.min(0.8, totalTrust / recentEvents.length);

        return {
            price: newPrice,
            confidence,
            reason: reasons.join("; ")
        };
    }

    private analyzeSentiment(content: string): 'positive' | 'negative' | 'neutral' {
        // Simplified sentiment analysis - in real world, use NLP
        const positiveWords = ['growth', 'profit', 'success', 'innovation', 'partnership'];
        const negativeWords = ['loss', 'decline', 'failure', 'lawsuit', 'scandal'];

        const contentLower = content.toLowerCase();
        const positiveCount = positiveWords.filter(word => contentLower.includes(word)).length;
        const negativeCount = negativeWords.filter(word => contentLower.includes(word)).length;

        if (positiveCount > negativeCount) return 'positive';
        if (negativeCount > positiveCount) return 'negative';
        return 'neutral';
    }
} 