import { IStock } from '../types';

export class MarketFearIndex {
    private static readonly MAX_HISTORY = 30; // Days of history to maintain
    private history: Array<{ timestamp: Date; value: number }> = [];

    calculate(stocks: Array<IStock> | Record<string, IStock>): number {
        // Convert input to array if it's not already
        const stocksArray = Array.isArray(stocks) ? stocks : Object.values(stocks);

        // Calculate the current fear index value (0-100)
        const weightedVolatility = this.calculateWeightedVolatility(stocksArray);
        const volumeChange = this.calculateVolumeChange(stocksArray);
        const priceDownside = this.calculatePriceDownside(stocksArray);

        // Combine factors into a single index (0-100)
        const fearIndex = (
            weightedVolatility * 0.5 + // 50% weight to market volatility
            volumeChange * 0.3 +      // 30% weight to volume changes
            priceDownside * 0.2       // 20% weight to price downsides
        ) * 100;

        // Add to history
        this.history.push({
            timestamp: new Date(),
            value: fearIndex
        });

        // Maintain history length
        if (this.history.length > MarketFearIndex.MAX_HISTORY) {
            this.history.shift();
        }

        return fearIndex;
    }

    private calculateWeightedVolatility(stocks: IStock[]): number {
        // Weight larger companies more heavily
        const totalMarketCap = stocks.reduce((sum, stock) =>
            sum + stock.company.financials.marketCap, 0);

        return stocks.reduce((weighted, stock) => {
            const weight = stock.company.financials.marketCap / totalMarketCap;
            return weighted + (stock.volatility * weight);
        }, 0);
    }

    private calculateVolumeChange(stocks: IStock[]): number {
        // Calculate average volume change across all stocks
        return stocks.reduce((sum, stock) => {
            const recentVolumes = stock.tradingVolume.slice(-2);
            if (recentVolumes.length < 2) return sum;

            const volumeChange = (recentVolumes[1].volume - recentVolumes[0].volume)
                / recentVolumes[0].volume;
            return sum + volumeChange;
        }, 0) / stocks.length;
    }

    private calculatePriceDownside(stocks: IStock[]): number {
        // Calculate percentage of stocks with negative returns
        const stocksDown = stocks.filter(stock =>
            stock.getPriceChangePercentage() < 0).length;
        return stocksDown / stocks.length;
    }

    getHistory(): Array<{ timestamp: Date; value: number }> {
        return [...this.history];
    }
} 