import BaseEntity from './BaseEntity';
import StockClass from './Stock';
import { IMarketData, ITradeResult, IStock, IStockData } from '../types';
import { AlgorithmPipeline } from '../algorithms/AlgorithmPipeline';
import { MovingAverageLayer } from '../algorithms/layers/MovingAverageLayer';
import { NewsAnalysisLayer } from '../algorithms/layers/NewsAnalysisLayer';
import { VolatilityLayer } from '../algorithms/layers/VolatilityLayer';
import { MarketFearIndex } from '../algorithms/MarketFearIndex';
import { Portfolio } from './Portfolio';
import { IUserPortfolio } from '../types/portfolio';
import { NewsItem } from '../types/news';
import { loadNewsData } from '../data/newsData';
import { setCurrentNews, addToHistory } from '../store/newsSlice';
import { GAME_CONFIG } from '../config/gameConfig';

/**
 * Market entity manages all stocks and trading operations
 */
export default class Market extends BaseEntity {
    private stocks: { [symbol: string]: IStock } = {};
    private userPortfolio: Portfolio;
    private lastUpdate: Date;
    private pricePipeline: AlgorithmPipeline;
    private fearIndex: MarketFearIndex;
    private updateInterval: NodeJS.Timeout | null = null;
    private newsUpdateInterval: NodeJS.Timeout | null = null;
    private currentNewsIndex: number = 0;
    private newsItems: NewsItem[] = [];
    private isGameActive: boolean = false;

    constructor() {
        super();
        this.lastUpdate = new Date();
        this.fearIndex = new MarketFearIndex();
        this.userPortfolio = new Portfolio();

        // Initialize algorithm pipeline
        this.pricePipeline = new AlgorithmPipeline();
        this.pricePipeline.addLayer(new MovingAverageLayer(20, 0.3));
        this.pricePipeline.addLayer(new NewsAnalysisLayer(0.2));
        this.pricePipeline.addLayer(new VolatilityLayer(14, 0.2));

        // Load news data
        this.newsItems = loadNewsData();

        // Start updates
        this.startGlobalUpdates();
        console.log('Market instance created');
    }

    public startGlobalUpdates(): void {
        //   
        this.updateMarket();

        //   
        if (!this.updateInterval) {
            this.updateInterval = setInterval(() => {
                this.updateMarket();
            }, GAME_CONFIG.PRICE_UPDATE_INTERVAL);
        }

        //   
        if (!this.newsUpdateInterval) {
            this.newsUpdateInterval = setInterval(() => {
                this.processNextNews();
            }, GAME_CONFIG.NEWS_INTERVAL);
        }
    }

    private simulateMarketActivity(stock: IStock): void {
        //    
        const volatility = 0.02; // 2%  
        const randomMove = (Math.random() - 0.5) * volatility * 2;
        const newPrice = stock.currentPrice * (1 + randomMove);

        //  
        const baseVolume = Math.floor(stock.currentPrice * 100);
        const buyVolume = Math.floor(baseVolume * (0.5 + Math.random()));
        const sellVolume = Math.floor(baseVolume * (0.5 + Math.random()));

        //  
        stock.addVolume(buyVolume, 'BUY');
        stock.addVolume(sellVolume, 'SELL');

        //  
        stock.updatePrice(Math.max(1, newPrice));
    }

    /**
     * Adds a stock to the market
     * @param stock - Stock instance to add
     */
    addStock(stock: IStock): void {
        this.stocks[stock.company.symbol] = stock;
        console.log(`Added stock: ${stock.company.symbol}`);
    }

    /**
     * Gets a stock by its symbol
     * @param symbol - Stock symbol
     * @returns Stock instance or undefined if not found
     */
    getStock(symbol: string): IStock | undefined {
        return this.stocks[symbol];
    }

    /**
     * Executes a buy order
     * @param symbol - Stock symbol
     * @param quantity - Number of shares to buy
     * @returns Trade result
     */
    executeBuyOrder(symbol: string, quantity: number): ITradeResult {
        const stock = this.stocks[symbol];

        if (!stock) {
            return {
                success: false,
                message: `Stock ${symbol} not found`
            };
        }

        // Get the latest price from Redux store
        const store = require('../store/store').store;
        const state = store.getState();
        const currentPrice = state.stocks.stocks[symbol]?.currentPrice ?? stock.currentPrice;

        const result = store.dispatch(require('../store/userSlice').executeTrade({
            symbol,
            quantity,
            price: currentPrice,
            type: 'BUY'
        }));

        // Log the trade details
        console.log('Executing buy order:', {
            symbol,
            quantity,
            currentPrice,
            stockPrice: stock.currentPrice,
            total: currentPrice * quantity
        });

        return {
            success: true,
            message: 'Trade executed successfully',
            transaction: {
                symbol,
                quantity,
                price: currentPrice,
                total: currentPrice * quantity,
                type: 'BUY',
                timestamp: new Date()
            }
        };
    }

    /**
     * Executes a sell order
     * @param symbol - Stock symbol
     * @param quantity - Number of shares to sell
     * @returns Trade result
     */
    executeSellOrder(symbol: string, quantity: number): ITradeResult {
        const stock = this.stocks[symbol];

        if (!stock) {
            return {
                success: false,
                message: `Stock ${symbol} not found`
            };
        }

        // Get the latest price and holdings from Redux store
        const store = require('../store/store').store;
        const state = store.getState();
        const currentPrice = state.stocks.stocks[symbol]?.currentPrice ?? stock.currentPrice;
        const holding = state.user.holdings[symbol];

        if (!holding || holding.quantity < quantity) {
            return {
                success: false,
                message: `Insufficient shares to sell. You own ${holding?.quantity || 0} shares.`
            };
        }

        store.dispatch(require('../store/userSlice').executeTrade({
            symbol,
            quantity,
            price: currentPrice,
            type: 'SELL'
        }));

        // Log the trade details
        console.log('Executing sell order:', {
            symbol,
            quantity,
            currentPrice,
            stockPrice: stock.currentPrice,
            total: currentPrice * quantity
        });

        return {
            success: true,
            message: 'Trade executed successfully',
            transaction: {
                symbol,
                quantity,
                price: currentPrice,
                total: currentPrice * quantity,
                type: 'SELL',
                timestamp: new Date()
            }
        };
    }

    /**
     * Gets current market data
     * @returns Market data including all stocks
     */
    getMarketData(): IMarketData {
        console.log('Getting market data, stocks:', Object.keys(this.stocks).length);
        return {
            stocks: { ...this.stocks },
            timestamp: new Date()
        };
    }

    /**
     * Updates prices for all stocks based on events and market conditions
     */
    updateMarket(): void {
        if (!this.isGameActive) return;

        console.log('Market update cycle started:', new Date().toLocaleTimeString());

        // Get latest stocks from Redux store
        const store = require('../store/store').store;
        const state = store.getState();
        this.stocks = { ...state.stocks.stocks };

        Object.values(this.stocks).forEach(stock => {
            this.simulateMarketActivity(stock);
        });

        // Dispatch updated stocks to Redux store
        store.dispatch(require('../store/stocksSlice').updateStocks(Object.values(this.stocks)));

        console.log('Market update cycle completed:', new Date().toLocaleTimeString());
        this.lastUpdate = new Date();
    }

    private determineTrend(stock: IStock): 'bullish' | 'bearish' | 'neutral' {
        const change = stock.getPriceChangePercentage();
        if (change > 1) return 'bullish';
        if (change < -1) return 'bearish';
        return 'neutral';
    }

    getFearIndex(): number {
        return this.fearIndex.calculate(Object.values(this.stocks));
    }

    getFearHistory(): Array<{ timestamp: Date; value: number }> {
        return this.fearIndex.getHistory();
    }

    getUserPortfolio(): IUserPortfolio {
        return this.userPortfolio;
    }

    // Method to clean up when component unmounts
    public cleanup(): void {
        if (this.updateInterval) {
            clearInterval(this.updateInterval);
            this.updateInterval = null;
        }
        if (this.newsUpdateInterval) {
            clearInterval(this.newsUpdateInterval);
            this.newsUpdateInterval = null;
        }
    }

    private processNextNews(): void {
        if (!this.isGameActive) return;

        const store = require('../store/store').store;
        const availableNews = this.newsItems.filter(news =>
            !store.getState().news.newsHistory.includes(news)
        );

        if (availableNews.length > 0) {
            const randomIndex = Math.floor(Math.random() * availableNews.length);
            const news = availableNews[randomIndex];

            // Dispatch news to Redux
            store.dispatch(setCurrentNews(news));
            store.dispatch(addToHistory(news));

            // Apply news impact to affected stocks
            news.affectedStocks.forEach(symbol => {
                const stock = this.stocks[symbol];
                if (stock) {
                    const impact = news.sentiment === 'positive' ? 1 + (news.impactLevel * 0.1) : 1 - (news.impactLevel * 0.1);
                    const newPrice = stock.currentPrice * impact;
                    stock.updatePrice(newPrice);
                }
            });
        }
    }

    public startGame(): void {
        this.isGameActive = true;
        this.currentNewsIndex = 0;
        this.startGlobalUpdates();
    }

    public stopGame(): void {
        this.isGameActive = false;
        if (this.updateInterval) {
            clearInterval(this.updateInterval);
            this.updateInterval = null;
        }
    }
} 